.container {
  font-family: "Brown2018-Regular";
  font-size: 16px;
  color: white; }
  .container--first {
    background-color: #000;
    height: 30px;
    display: flex;
    justify-content: space-around; }
  .container--second {
    background-color: #191e25;
    height: 80px; }

.nav--left {
  display: flex;
  flex-direction: row;
  width: 30%;
  cursor: pointer; }

.nav_img {
  height: 15px;
  margin: auto; }

.nav_items {
  margin: auto; }
  .nav_items-button {
    background-color: transparent;
    color: white;
    border-radius: 15px;
    cursor: pointer; }

.nav--right {
  display: flex;
  justify-content: space-around;
  width: 60%;
  cursor: pointer; }

.menu {
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: auto;
  width: 60%;
  height: 100%; }
  .menu_items {
    font-family: "Brown2018-Regular";
    margin: auto;
    opacity: .8;
    transition: opacity .1s ease-out;
    cursor: pointer; }
    .menu_items:hover {
      opacity: 1; }
  .menu-image {
    margin: auto; }
  .menu-search:after {
    content: "";
    background-image: url("/static/search.png");
    display: inline-block;
    width: 20px;
    height: 20px;
    margin-left: 10px; }
